body {
  background-color: #f0f0f0;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

.container {
  max-width: 1200px;
  text-align: center;
  margin: 0 auto;
  padding: 20px;
}

.search-form {
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

.search-input {
  width: 300px;
  border: 2px solid #ccc;
  border-radius: 4px;
  padding: 10px;
  font-size: 16px;
}

.search-btn {
  color: #fff;
  cursor: pointer;
  background-color: #007bff;
  border: none;
  border-radius: 4px;
  margin-left: 10px;
  padding: 10px 20px;
  font-size: 16px;
}

.search-btn:hover {
  background-color: #0056b3;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  display: flex;
}

.list {
  width: 250px;
  background-color: #fff;
  border-radius: 8px;
  list-style: none;
  transition: transform .3s;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .1);
}

.list:hover {
  filter: drop-shadow(0 0 .75rem #111);
  transform: scale(1.05);
}

.photo-link {
  width: 100%;
  height: 200px;
  display: block;
  overflow: hidden;
}

.picture {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: transform .3s;
}

.list:hover .picture {
  transform: scale(1.1);
}

.info {
  text-align: left;
  padding: 15px;
}

.info p {
  color: #555;
  margin: 5px 0;
  font-size: 14px;
}

.info p b {
  color: #000;
}

.more-bt {
  text-align: center;
  justify-content: center;
  align-items: center;
  margin: 20px 0;
  display: flex;
}

.load-more {
  color: #fff;
  cursor: pointer;
  background-color: #28a745;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
}

.load-more:hover {
  background-color: #218838;
}

